class View {
  constructor(game, $el) {
    this.game = game;
    this.$el = $el;

    this.setupBoard();
  }

  bindEvents() {}

  makeMove($square) {}

  setupBoard() {
    const $board = $("<ul>");
    $board.addClass("group");

    for (let rowIdx = 0; rowIdx < 3; rowIdx += 1) {
      for (let colIdx = 0; colIdx < 3; colIdx += 1) {
        const $cell = $("<li>");
        $cell.data("pos", [rowIdx, colIdx]);

        $board.append($cell);
      }
    }

    this.$el.append($board);
  }
}

module.exports = View;



